/* Pretty printing styles. Used with prettify.js. */
/* Vim sunburst theme by David Leibovic */

pre .str, code .str {
  color: #65B042;
}

/* string  - green */
pre .kwd, code .kwd {
  color: #E28964;
}

/* keyword - dark pink */
pre .com, code .com {
  color: #AEAEAE;
  font-style: italic;
}

/* comment - gray */
pre .typ, code .typ {
  color: #89bdff;
}

/* type - light blue */
pre .lit, code .lit {
  color: #3387CC;
}

/* literal - blue */
pre .pun, code .pun {
  color: #fff;
}

/* punctuation - white */
pre .pln, code .pln {
  color: #fff;
}

/* plaintext - white */
pre .tag, code .tag {
  color: #89bdff;
}

/* html/xml tag    - light blue */
pre .atn, code .atn {
  color: #bdb76b;
}

/* html/xml attribute name  - khaki */
pre .atv, code .atv {
  color: #65B042;
}

/* html/xml attribute value - green */
pre .dec, code .dec {
  color: #3387CC;
}

/* decimal - blue */

pre.prettyprint, code.prettyprint {
  background-color: #000;
  -moz-border-radius: 8px;
  -webkit-border-radius: 8px;
  -o-border-radius: 8px;
  -ms-border-radius: 8px;
  -khtml-border-radius: 8px;
  border-radius: 8px;
}

pre.prettyprint {
  margin: 1em auto;
  padding: 1em;
  white-space: pre-wrap;
}

/* Specify class=linenums on a pre to get line numbering */
ol.linenums {
  margin-top: 0;
  margin-bottom: 0;
  color: #AEAEAE;
}

/* IE indents via margin-left */
li.L0, li.L1, li.L2, li.L3, li.L5, li.L6, li.L7, li.L8 {
  list-style-type: none
}

/* Alternate shading for lines */
li.L1, li.L3, li.L5, li.L7, li.L9 {
}

@media print {
  pre .str, code .str {
    color: #060;
  }

  pre .kwd, code .kwd {
    color: #006;
    font-weight: bold;
  }

  pre .com, code .com {
    color: #600;
    font-style: italic;
  }

  pre .typ, code .typ {
    color: #404;
    font-weight: bold;
  }

  pre .lit, code .lit {
    color: #044;
  }

  pre .pun, code .pun {
    color: #440;
  }

  pre .pln, code .pln {
    color: #000;
  }

  pre .tag, code .tag {
    color: #006;
    font-weight: bold;
  }

  pre .atn, code .atn {
    color: #404;
  }

  pre .atv, code .atv {
    color: #060;
  }
}

.fb-like {
  height: 25px !important;
  overflow: hidden;
}

.no-select, [contenteditable=false] {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;

}

.no-select ::selection,  [contenteditable=false] ::selection,
.no-select ::-moz-selection,  [contenteditable=false] ::-moz-selection {
  background: rgba(0,0,0,0);
}

/* BODY {
  background: #203a56;
} */

.clear {
  clear: both;
}

.window {
  border: 1px solid #d6d6ce;
  border-right-color: #424242;
  border-bottom-color: #424242;
  font-family: "Tahoma", sans-serif;
  font-size: 11px;
}

.window-border {
  border: 1px solid #ffffff;
  border-right-color: #848484;
  border-bottom-color: #848484;
  background: #d6d6ce;
  padding: 2px;
}

/****
* Title bar
***/

.title-bar {
  padding: 0;
  background: #08216b; /* Old browsers */
  background: -moz-linear-gradient(left, #08216b 0%, #a5cef7 100%); /* FF3.6+ */
  background: -webkit-gradient(linear, left top, right top, color-stop(0%, #08216b), color-stop(100%, #a5cef7)); /* Chrome,Safari4+ */
  background: -webkit-linear-gradient(left, #08216b 0%, #a5cef7 100%); /* Chrome10+,Safari5.1+ */
  background: -o-linear-gradient(left, #08216b 0%, #a5cef7 100%); /* Opera 11.10+ */
  background: -ms-linear-gradient(left, #08216b 0%, #a5cef7 100%); /* IE10+ */
  background: linear-gradient(left, #08216b 0%, #a5cef7 100%); /* W3C */
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr = '#08216b', endColorstr = '#a5cef7', GradientType = 1); /* IE6-9 */
  color: #fff;
  font-weight: bold;
}

.title-bar .title {
  padding-left: 20px;
  display: block;
  background: url(./img/window/word.png) no-repeat left center;
  white-space: nowrap;
  overflow: hidden;              /* "overflow" value must be different from "visible" */

  text-overflow:    ellipsis;
}

.control-box {
  float: right;
  margin: 2px;
}

.button-3d {
  display: block;
  float: left;
  border: 1px solid #ffffff;
  border-right-color: #424242;
  border-bottom-color: #424242;
  background: #d6d6ce;
  height: 14px;
  width: 16px;
  color: transparent;
  text-decoration: none;
  cursor: pointer;
  background-position: 1px 1px;
  background-repeat: no-repeat;
}

.button-3d span {
  display: block;
  border: 1px solid #848484;
  border-top-width: 0;
  border-left-width: 0;
  font-size: 1px;
  line-height: 13px;
}

.button-3d.maximize {
  margin-right: 2px;
  margin-left: 1px;
}

.button-3d.close {
  background-image: url(./img/window/close.png);
}

.button-3d.maximize {
  background-image: url(./img/window/maximize.png);
}

.button-3d.minimize {
  background-image: url(./img/window/minimize.png);
}

/****
* Menu bar
***/

.menu-bar, .toolbar {
  height: 24px;
  overflow: hidden;
  margin: 2px 1px;
  padding:0 7px;
  background: url(./img/window/toolbar-grip.png) no-repeat 2px 6px;
}
.menu-bar{
  line-height: 24px;
}

.menu-bar a {
  text-decoration: none;
  display: inline-block;
  padding: 0px 9px;
}

.menu-bar a span {
  text-decoration: underline;
}

/****
* Toolbar
***/

.toolbar {
  background-color: #deded6;
  border-radius: 1px;
}

.toolbar a {
  display: inline-block;
  border: 1px solid transparent;
  margin: 2px 0px;
  cursor: pointer;
  text-decoration: none;
  width: 18px;
  height: 18px;
  background: no-repeat center left;
}

.toolbar .sep {
  display: inline-block;
  width: 1px;
  background: #a5a5a5;
  margin: 1px 3px;
  height: 20px;
}

.toolbar.float-left{
  float:left;
  margin-right: 3px;
}

.toolbar.float-right{
  float:right;
}

.toolbar .b-new {
  background-image: url(./img/window/toolbar/new.png);
}

.toolbar .b-open {
  background-image: url(./img/window/toolbar/open.png);
}

.toolbar .b-save {
  background-image: url(./img/window/toolbar/save.png);
}

.toolbar .b-print {
  background-image: url(./img/window/toolbar/print.png);
}

.toolbar .b-spell {
  background-image: url(./img/window/toolbar/spell.png);
}

.toolbar .b-copy {
  background-image: url(./img/window/toolbar/copy.png);
}

.toolbar .b-paste {
  background-image: url(./img/window/toolbar/paste.png);
}

.toolbar .b-undo {
  background-image: url(./img/window/toolbar/undo.png);
}

.toolbar .b-redo {
  background-image: url(./img/window/toolbar/redo.png);
}

.toolbar .b-style {
  background-image: url(./img/window/toolbar/style.png);
}

.toolbar .b-bold {
  background-image: url(./img/window/toolbar/bold.png);
}

.toolbar .b-italic {
  background-image: url(./img/window/toolbar/italic.png);
}

.toolbar .b-underline {
  background-image: url(./img/window/toolbar/underline.png);
}

.toolbar .b-left {
  background-image: url(./img/window/toolbar/left.png);
}

.toolbar .b-center {
  background-image: url(./img/window/toolbar/center.png);
}

.toolbar .b-right {
  background-image: url(./img/window/toolbar/right.png);
}

.toolbar a:hover {
  background-color: #d6d6de;
  border-color: #08216b;
}

.toolbar a.text{
  width:auto;
  padding-left:22px;
  padding-right:4px;
  line-height: 18px;
  background-position: 2px 1px;
}

.toolbar a.text em{
  font-style: normal;
  display: inline-block;
  padding:2px;
  font-size: 10px;
  line-height: 10px;
  background: #d8e6ff;
  border:1px solid #c2d3f4;
  border-radius: 3px;
  margin-left: 5px;
}



.toolbar .picker {
  line-height: 18px;
  height: 18px;
  background: #fff;
  margin: 4px 0 2px 0;
  display: inline-block;
  padding-left:3px;
  position: relative;
  vertical-align: top;
}

.toolbar .picker .arrow{
  display: block;
  float:right;
  width:11px;
  background: #deded6 url(./img/window/toolbar/combo.png) no-repeat center center;
  height:16px;
  margin:1px 1px 1px 10px;
}

/****
* Content
***/

.content {
  border: 1px solid #848484;
  border-right-color: #ffffff;
  border-bottom-color: #ffffff;
}

.content-box {
  border: 1px solid #424242;
  border-right-color: #d6d6ce;
  border-bottom-color: #d6d6ce;
  background: #848484;
  position: relative;
}

/****
* Rullers
****/

.top-ruler {
  height: 24px;
  background: #d6d6ce url(./img/window/ruler-mark.png) no-repeat left top;
  padding-left: 25px;
}

.top-ruler .ruler {
  background: url(./img/window/horz-ruler.png);
  height: 24px;
  margin-left: 2px;
  margin-right: 20px;
}

.left-ruler {
  position: relative;
  width: 24px;
  background: #d6d6ce;
  float: left;
}

/****
* Document
***/

.document-scroller {
  overflow-y: scroll;
  margin-left: 24px;
}

.document-content {
}

.document {
  margin: 5px auto 10px auto;
  background: #fff;
  border: 1px solid #000;
  box-shadow: 1px 1px 0 0 #000;
  min-height: 1000px;
}

.document-wrap {
  padding: 35px;
  outline: none;
}

/****
* Status bar
***/

.status-bar {
  margin-top: 5px;
  border-top: 1px solid #848484;
  background: url(./img/window/status-bar-grip.png) no-repeat right bottom;
}

.status-bar-content {
  border-top: 1px solid #fff;
  padding: 4px 2px 2px 5px;
}

.status-bar .box {
  display: inline-block;
  border: 1px solid #e7dede;
  padding: 0px 5px;
  margin-right: 5px;
}

.status-bar .box.disabled {
  color: #848484;
}

